
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import { OfficialsealList, startApplication, cancelTask } from '@/api/'
import { mapGetters } from 'vuex'
export default {
name: 'overTimeLeave',
components: {
PopupPicker, Group, XInput, PopupRadio, Datetime, XTextarea, Loading
},
data () {
return {
Officialseal: [],
loading: false, //
startTime: '',
endTime: '',
form: {
type: 12,
companyId: '',
loanItemId: '',
loanTime: '',
returnTime: '',
reason: ''
}
}
},
methods: {
cancelTask () {
const data = {
taskId: this.taskId
}
const that = this
this.$vux.confirm.show({
title: '提示',
content: '是否取消申请',
onConfirm () {
cancelTask(data)
.then(res => {
that.$vux.toast.show({
text: res.message,
type: 'success'
})
that.$router.go(-1)
})
}
})
},
changeLoanTime (val) {
this.form.loanTime = val + ':00'
},
changeReturnTime (val) {
this.form.returnTime = val + ':00'
},
showseal () {
this.Officialseal = []
this.getOfficialsealList()
},
getOfficialsealList () {
var allsealList = []
const data = {
companyId: this.form.companyId,
type: 1
}
OfficialsealList(data).then(res => {
allsealList = res.result
allsealList.forEach(e => {
this.Officialseal.push({key: e.id, value: e.name})
})
})
},
startLeave () {
startApplication(this.form)
.then(res => {
this.$vux.toast.show({
text: res.message,
type: 'success'
})
this.$router.go(-1)
})
}
},
computed: {
...mapGetters(['applyLeaveList'])
}
}



